import axios from 'axios';

const myInitMethods = {
  method: 'GET',
  mode: 'cors',
};

const API_URL = 'https://swapi.dev/api/';

export const getFilmsPage = () => fetch(`${API_URL}films`);

export const getPeoplePage = () =>
  fetch(`${API_URL}people`.replace('http', 'https'));

export const getPlanetsPage = () =>
  fetch(`${API_URL}planets`.replace('http', 'https'));

export const getSpeciesPage = () =>
  fetch(`${API_URL}species`.replace('http', 'https'));

export const getStarshipsPage = () =>
  fetch(`${API_URL}starships`.replace('http', 'https'));

export const getVehiclesPage = () =>
  fetch(`${API_URL}vehicles`.replace('http', 'https'));
